

.main-logo-wrapper{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 2px 10px;
    border-radius: 1px;
    
    border-bottom: 1px solid rgb(0, 255, 213);
    border-left: 32px solid rgb(0, 255, 213);
}

.main-logo-image{
    width: 60px;
    height: 40px;
}



.main-logo-text{
    padding: 0 10px;
    font-size: 12px;
}



