.email-form{
    width: 60vw;
    height: 80vh;
    position: fixed;
    top: 10%;
    left: 20%;
    border-radius: 0px;
    background-color: rgb(255, 255, 255);
    box-shadow: 2px 3px 5px rgb(37, 35, 35);
    animation-name: MoveDown;
    animation-duration: 400ms;
    border-radius: 2vw;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}


.contact-form {
    margin: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.user_name_field {
    background-color: rgb(189, 187, 187);
    border-radius: 10px;
    margin-left: 1vw;
    height: 6vh;
    width: 22vw;
    border: none;
    display: block;

}

.user_email_field {
    background-color: rgb(189, 187, 187);
    border-radius: 10px;
    border: none;
    display: block;
    height: 6vh;
    width: 22vw;
    margin-left:1vw;
}


.user_message_field {
    background-color: rgb(189, 187, 187);
    border-radius: 10px;
    margin-left: 1vw;
    border: none;
    display: block;
    height: 20vh;
    width: 23vw
}



.email-form-left-side{
    width: 100%;
    height:80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    
}


.email-form-right-side{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: turquoise;
    clip-path: polygon(0% 0%, 90% 10%, 100% 80%, 20% 100%);
}

.email-svg{
    width: 25vw;
    height: 50vh;
}



.send-message-email{
    position: relative;
    width: 20vw;
    height: 5vh;
    border-radius: 1vw;
    border: none;
    background-color: rgb(229, 226, 226);
    margin: 0 auto;
    padding: 10px;
    box-shadow: 2px 2px 2px rgb(204, 204, 204) inset;
}

.send-message-email::placeholder {
    position: absolute;
    font-size: 12px;
    color:rgb(153, 152, 152);
    font-family: 'text-font';
    font-weight: 700;

}

.send-email-button-wrapper{
    width: 20vw;
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.email-name {
    font-size: 15px;
    font-weight: 800;
    font-family: 'text-font';
}

.email-name-ctn {
    margin: 5px
}

.email-email-ctn{
    margin: 5px;
}

.email-message-ctn {
    margin: 5px;
}
.message-lable {
    font-size: 15px;
    font-weight: 800;
    font-family: 'text-font';
}

.name-lable {
    font-size: 15px;
    font-weight: 800;
    font-family: 'text-font';
}


.send-email-button{
    width: 16vw;
    height: 8vh;
    background-color: blue;
    border-radius: 10px;
    color: #fff;
    font-family: 'text-font';
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1vw;
    cursor: pointer;
    margin: 0.4vw;
}



.send-email-button:hover{
    background-color: rgb(8, 212, 195);
}





.sender-email-wrapper{
    display: flex;
    width: 19vw;
    height: 10vh;
    flex-direction: column;
}

.sender-email{
    border-bottom: 1px solid rgb(196, 195, 195);
    border-top: none;
    border-left: none;
    border-right: none;
}

.sender-email-lable{
    color: gray;
    font-family: 'text-font';
    font-size: 12px;
}

@keyframes MoveDown{
    0%{
        transform: translateY(-500px);

    }

    100%{
        transform: translateY(0);
    }
}