.home-page-see-projects-button-wrapper{
    width: 13vw;
    height: 2.5vw;
    display: inline-block;
    position: relative;
}



.home-page-see-projects-button {
width: 100%;
height: 100%;
display: flex;
text-align: center;
justify-content: center;
align-items: center;
font-size: 10px;
font-family: 'text-font';
color: #fff;
z-index: 2;
background-color: #6937ff;
border-radius:0.4vw;
box-shadow: 14px 6px 1px rgb(20, 253, 253);
cursor: pointer;
}


.home-page-see-projects-button:hover{
    background-color: rgb(253, 109, 205);
    animation-name: MoveUp;
    animation-duration: 300ms;
    box-shadow: 1px 1px 1px gray;
}









.button-arrows-sign{
    padding-left: 20px;
    font-size: 12px;
    font-weight: 800;
}


/* 
/* Creating animation for see projects button */

@keyframes MoveUp {
   0%{
          box-shadow: 14px 6px 1px rgb(42, 250, 222);
   }

   100%{
        box-shadow: 1px 1px 1px rgb(50, 247, 237);
   }
} 




