.about-page-navbar-ctn{
    width: 100vw;
    margin: 0px auto;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.projects-heading {
    font-size: 20px;
    text-decoration: underline;
    color: black;
    margin: 0 auto;
    font-family: 'text-font';
    font-weight: 800;
}


.about-page-body-ctn{
    width: 100vw;
    height: 130vh;
    position: relative;
    margin: 20vh 0px;

}



.about-page-footer-ctn {
    position: relative;
    width: 100vw;
    height: 1vh;
}



.about-page-wrapper{
    overflow: hidden;
    align-items: center;
    display: flex;
    flex-direction: column;
}